
import Vue from "vue";
import DatePicker from "@/components/form-components/DatePicker.vue";
import store from "@/store";
import API from "@/api/API";
import CreateBeneficiary from "@/modules/orders/components/CreateBeneficiary.vue";
import ClientData from "@/modules/orders/components/ClientData.vue";
import rules from "@/services/helpers/validation-rules";
import { mask } from "vue-the-mask";
// import CommentsPreview from "@/modules/orders/components/CommentsPreview.vue";
import Payments from "@/modules/orders/components/Payments.vue";
import Services from "@/modules/orders/components/Services.vue";
import CarnetIssuance from "@/modules/orders/components/CarnetIssuance.vue";
import LiveSearch from "@/components/form-components/LiveSearch.vue";
import allowedFields from "../config/available-fields.json";
import FileUploader from "@/components/form-components/FileUploader.vue";
import Allocation from "@/modules/orders/components/Allocation.vue";

export default Vue.extend({
  name: "AtaCreateOrder",

  components: {
    Allocation,
    CarnetIssuance,
    Services,
    Payments,
    DatePicker,
    CreateBeneficiary,
    ClientData,
    LiveSearch,
    FileUploader
  },

  props: {
    value: {
      type: Object,
      required: true,
      default: () => ({})
    }
  },

  directives: { mask },

  data: () => ({
    allowedFields: allowedFields as any,
    rules,
    selectedField: "" as string,
    loading: false as boolean,
    loadingPayments: false as boolean,
    loadingCarnetIssused: false as boolean,
    selectedClient: null as null | SelectComponentInterface,
    selectedCard: null as null | SelectComponentInterface,
    model: {
      documents: [] as Array<any>,
      order: {
        subdivision_id: 1,
        branch_id: 1,
        source: "operator",
        client_delegate: "aaij",
        document_type: "new",
        client: { type: "" } as any,
        document_valid_from: new Date().toISOString().substr(0, 10),
        power_of_attorney: "beneficiary" as string
      } as any,
      details: { goods: [] as Array<any> } as any
    } as any,
    errorMessages: {} as any,
    selects: {
      cardTypes: [] as Array<SelectComponentInterface>,
      purpose: [] as Array<SelectComponentInterface>,
      packingCategories: [] as Array<SelectComponentInterface>,
      languages: [] as Array<SelectComponentInterface>,
      client_types: [] as Array<SelectComponentInterface>,
      release_types: [] as Array<SelectComponentInterface>,
      areas: ["agro", "other"] as Array<string>,
      currencies: ["agro", "other"] as Array<string>
    } as any,
    lang: store.getters["localization/getCurrent"],
    selectedTab: 1,
    totalSum: 0 as number,
    createBeneficiaryModal: false as boolean,
    isShowDocuments: true as boolean,
    isShowDocumentsNumber: false as boolean,
    isLoading: false,
    breadcrumb: [
      {
        text: "orders.title",
        disabled: false,
        href: "/orders"
      },
      {
        text: "orders.form.header.create",
        disabled: true,
        href: `/orders/edit`
      }
    ],
    defaultCountry: null as any,
    comments: {} as any,
    oldModel: null as any,
    forceKey: 0 as number,
    regions: [
      { text: "Moldova", value: "moldova" },
      { text: "Transnistria", value: "transnistria" }
    ],
    powerOfAttorney: null as any
  }),

  watch: {
    value: {
      immediate: true,
      handler() {
        this.model = this.value;

        if (this.model.hasOwnProperty("additions")) {
          if (this.model.additions.hasOwnProperty("notes")) {
            this.selectedTab = 1;
            this.comments = this.parseComments(this.model.additions.notes);
          }
        }
      }
    },
    "model.documents": {
      immediate: true,
      deep: true,
      handler() {
        this.$forceUpdate();
      }
    }
  },

  computed: {
    canChangedReleaseMode() {
      return this.oldModel?.order.release_mode === "urgent";
    },
    isViewCarnetIssuance() {
      return this.model.order?.status?.alias === "paid";
    },
    isDataChanged() {
      if (this.model && this.oldModel) {
        return JSON.stringify(this.model) !== JSON.stringify(this.oldModel);
      }
      return false;
    },
    isOriginalDocument() {
      return this.model.order.document_type === "new";
    },
    formattedDate() {
      return this.$moment(this.model.order.document_valid_from, "YYYY-MM-DD")
        .add(1, "years")
        .subtract(1, "days")
        .format("DD.MM.YYYY");
    },
    areas() {
      return this.selects.areas.map((item: any) => ({
        value: item,
        text: this.$t(`orders.areas.${item}`)
      }));
    },
    isBeneficiaryAndProcessing() {
      return (
        this.model.order.status.id === 1 && this.model.order.source === "client"
      );
    },
    orderFulfilled() {
      return this.model.order.status.id === 4;
    },

    goodsSum() {
      return this.model.details.goods.reduce(
        (
          total: number,
          { price, quantity }: { price: number; quantity: number }
        ) => {
          if (price && quantity) {
            return total + price * quantity;
          }
          return total;
        },
        0
      );
    },
    currency() {
      return this.selects.currencies.find(
        (item: any) => item.value === this.model.details.currency_id
      );
    }
  },

  async mounted() {
    await this.loadData();
  },

  methods: {
    setDefaultTab() {
      if (
        this.model.order.status &&
        ["processing", "issued", "canceled"].includes(
          this.model.order.status.alias
        )
      ) {
        this.selectedTab = 1;
        return;
      } else {
        this.selectedTab = 3;
      }
    },
    isReadonly(field: string) {
      return this.allowedFields?.[this.model.order.status?.alias]?.includes(
        field
      );
    },
    changeKey() {
      this.forceKey = this.forceKey++;
    },
    getOrderSum(ev: any) {
      this.totalSum = ev;
    },
    async getData() {
      this.$emit("update");
    },
    onTabChange(tabIndex: number): void {
      this.loadingPayments = tabIndex === 2;
      this.loadingCarnetIssused = tabIndex === 3;
    },
    parseComments(array: any) {
      const object = {} as any;

      for (const key of array) {
        object[key.key] = key;
      }
      return object;
    },
    isSelectBeneficiary(): boolean {
      return !this.model.order.client_id;
    },
    async loadData() {
      this.loading = true;
      try {
        const headers = {
          "x-localization": this.model.order.language
        };

        const response = await Promise.all([
          this.$API
            .purposeOfUse()
            .getList(this.model.order.sertice_type_id, headers),
          this.$API.orders().getOrderReleaseModes(),
          this.$API.clients().getClientTypes(),
          this.$API.orders().getCardTypes(this.model.order.service_type_id),
          this.$API.orders().getLanguages(),
          this.$API.transportTypes().getList(headers),
          this.$API.employees().getListByService(this.serviceId),
          this.$API.goodsCategories().getList(),
          this.$API.countries().getAcceptAta(),
          this.$API.measureUnits().getList(this.model.order.service_type_id),
          this.$API.packingCategories().getList(),
          this.$API.currency().getList()
        ]);

        this.scanned_documents = await API.orders().getScannedDocuments(
          Number(this.$route.params.id)
        );

        await this.setServerResponse(response);
      } catch (e) {
        await store.dispatch("alert/showError", e.message);
      }
      this.loading = false;
    },
    async submit(): Promise<void> {
      this.isLoading = true;
      const form = this.$refs.form as Vue;

      try {
        if (form.validate()) {
          this.model.documents = this.model.documents.filter(
            (item: any) => item.file_type !== "confirm_represent_power"
          );

          if (this.powerOfAttorney.file) {
            this.model.documents.push(this.powerOfAttorney);
          }

          const model = { ...this.model };

          await this.$API.orders().edit(Number(this.$route.params.id), model);
          form.updateGuard();
          await this.$emit("update");
          // await this.$router.push("/orders");
          await this.$store.dispatch(
            "alert/showSuccess",
            this.$t("global-alert.successful_edit")
          );
        } else {
          this.selectedTab = 1;
          await setTimeout(() => {
            const firstErrorInput = form.inputs.find(
              (item: any) => item.messagesToDisplay.length
            );

            if (firstErrorInput) {
              this.$vuetify.goTo(firstErrorInput.$el, {
                offset: 150
              });
            }
          }, 500);
        }
      } catch (e) {
        if (e.hasOwnProperty("errors")) {
          this.errorMessages = e.errors;
          if (e.errors.length) {
            this.selectedTab = 1;
          }

          await setTimeout(() => {
            const firstErrorInput = form.inputs.find(
              (item: any) => item.messagesToDisplay.length
            );

            if (firstErrorInput) {
              this.$vuetify.goTo(firstErrorInput.$el, {
                offset: 150
              });
            }
          }, 500);

          setTimeout(() => {
            this.errorMessages = {};
          }, 4000);
        }
        await this.$store.dispatch("alert/showError", e.message || e);
      }
      this.isLoading = false;
    },
    async setServerResponse(response: Array<any>) {
      const [
        purpose,
        releaseModes,
        clientTypes,
        cardTypes,
        languages,
        transportTypes,
        employees,
        goodsCategories,
        countries,
        units,
        packingCategories,
        currencies
      ] = response;

      this.selects.languages = languages.items;
      this.selects.purpose = purpose;
      this.selects.release_types = releaseModes.items;
      this.selects.client_types = clientTypes;
      this.selects.cardTypes = cardTypes.items;
      this.selects.transportTypes = transportTypes.items;
      this.selects.employees = employees.items;
      this.selects.goodsCategories = goodsCategories;
      this.selects.countries = countries.items;
      this.selects.units = units;
      this.selects.packingCategories = packingCategories;
      this.selects.currencies = currencies.items;

      this.$nextTick(() => {
        this.oldModel = JSON.parse(JSON.stringify(this.model));
        this.$forceUpdate();
      });
    },
    async search(value: string, key: string): Promise<any> {
      try {
        return await (this.$API as any)[key]().liveSearch(value);
      } catch (e) {
        await this.$store.dispatch("alert/showError");
      }
    },
    async searchClient(value: string, key: string): Promise<any> {
      try {
        return {
          items: await (this.$API as any)[key]().liveSearchClient(value)
        };
      } catch (e) {
        await this.$store.dispatch("alert/showError");
      }
    },
    setClient(client: any): void {
      this.model.order.client_id = client.id;
      this.model.order.client = client;
    },
    clearClient(): void {
      delete this.model.order.client_id;
      delete this.model.order.client;
    },
    changeData(items: Array<any>) {
      this.model.details.countries = items;
    },
    setParentCard(card: any) {
      if (card?.value) {
        this.model.order.parent_document_id = card.value;
      }
    },
    setPurposeDescription() {
      const index = this.selects.purpose.findIndex(
        (item: any) => item.value === this.model.details.purpose_id
      );

      if (index !== -1) {
        this.model.details.purpose_description = this.selects.purpose[
          index
        ].description;
      }
    },
    setBeneficiary(event: any) {
      this.selectedClient = { value: event.id, text: event.name };
      this.setClient(event);
    },
    loadDocuments(val: any) {
      this.model.documents = val;
    },
    loadScannedDocuments(val: any) {
      this.scanned_documents = val;
    },
    async sendScannedDocuments() {
      try {
        await this.$API
          .orders()
          .sendScannedDocuments(
            Number(this.$route.params.id),
            this.scanned_documents
          );
      } catch (e) {
        if (e.message) {
          await this.$store.dispatch("alert/showError", e.message);
        }
      }
    },

    changeOrderStatus(): void {
      this.$emit("showStatusDialog");
    },
    async setExchangeRate(currencyId: number): Promise<void> {
      try {
        const { value } = await this.$API
          .exchangeRates()
          .getExchangeRate(currencyId);
        this.model.details.exchange_rate = value;
        this.$forceUpdate();
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    addItem(): void {
      this.model.details.goods.push({
        name: ""
      });
    },
    addDocument(): void {
      this.model.documents.push({ file_type: "certification" });
    },
    removeDocument(index: number): void {
      this.model.documents.splice(index, 1);
    },
    async deleteRow(index: number): Promise<void> {
      this.model.details.goods.splice(index, 1);
    },
    setMeasureUnit(event: any, index: number) {
      this.model.details.goods[index].measure_unit_id = event.value;
      this.model.details.goods[index].measure_unit_name = event.text;
    },
    async cloneOrder(): Promise<void> {
      try {
        await this.$API.orders().cloneOrder(this.model.order.id);
        await this.$store.dispatch(
          "alert/showSuccess",
          this.$t("orders.modals.alert.successful_cloned")
        );
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    setPowerOfAttorney(event: any) {
      this.powerOfAttorney = { ...event };
    },
    setAllocations({ branch_id, subdivision_id, handler_id }: any): void {
      this.model.order.branch_id = branch_id;
      this.model.order.subdivision_id = subdivision_id;
      this.model.order.handler_id = handler_id;
    }
  }
});
